import React from 'react';
import './Testimonials.scss';
import Carousel from 'react-elastic-carousel';
import axios from 'axios';

const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 550, itemsToShow: 2, itemsToScroll: 2, pagination: false },
    { width: 850, itemsToShow: 3 },
    { width: 1150, itemsToShow: 4, itemsToScroll: 2 },
    { width: 1450, itemsToShow: 5 },
    { width: 1750, itemsToShow: 6 },
]
class Testemonia extends React.Component{
    state = {
        data : []
    }

    componentDidMount(){
        axios.get('./data.json').then
        ((res) => {this.setState({ data:res.data.getTestemonia})})

    }
        render(){
            const {data} = this.state;
                const alldata = data.map((item)=>{
                    return(
                        <div className="swiper-slider" key={item.id}>
                            <img src={item.image} alt="" />
                                <div className="content-text">
                                <div className="content-text-margin">{item.contentTextMargin} </div> 
                                <div className="testimonial-author">{item.testimonialauthor}</div>
                            </div>
                        </div>
                    )
                
                });
            return(
                <div className="testemonia">
                    <div className="container container-pr-pl">
                        <div className="row">
                            <div className="col">
                                <h2> Read Our Customer Testimonials </h2>
                            </div>
                            <div className="col">
                                <p> Our clients are our partners and we can not imagine a better future for our company without helping them reach their objectives </p>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                               <div className="slider-container">
                                    <div className="swiper-container card-slider swiper-container-horizontal">
                                        <div className="swiper-wrapper">
                                        <Carousel breakPoints = {breakPoints}>
                                            {alldata}
                                        </Carousel>
                                        </div>
                                    </div>           
                               </div>
                            </div>
                        </div>
    
                    </div>
                </div>
    
            )
            
        }
       



}

export default Testemonia;
